import { Injectable } from '@angular/core';
@Injectable()
export class Configuration {
    constructor() {
    }

  
    static appName = "Push Forever"
    iosVersiyon = "1.2";
    androidVersiyon = "1.5";
    static language = 2;
   
    GoogleAppId = "359350162247"
    userLoginUrl = "http://api.pushforever.online/api/users/UserControl";
    newUserUrl = "http://api.pushforever.online/api/users/";
    forgotPasswordUrl = "http://api.pushforever.online/api/users/ForgetPassword";
    saveRegIdUrl = "http://api.pushforever.online/api/devices/";
    announcementsUrl = "http://api.pushforever.online/api/announcements";


    ustBArRenk;



    createAuthorizationHeaders(headers: Headers): Headers {
    
        return headers;
    }
   
    EmailIsvalid(email: string): Boolean {

        var bol = email.split('@');
        if (bol.length != 2) {
            return false;
        }
        var bolikinci = bol[1].split('.');
        if (bolikinci.length <= 1) {
            return false;
        }

        return true;
    }
 
}



